import React from "react";
import "./FirstSection.css";
import "./Carousel.css";
import { Carousel } from "react-carousel-minimal";
import { CarouselData } from "./CarouselData";
import { Container, Typography } from "@mui/material";
import { Grid } from "@mui/material";
import MainButton from "../MainButton";


import imgPropuesta from "../../assets/images/us.jpg";
import imgRecomendados from "../../assets/images/menuFinal.PNG";
import MainTitle from "../Texts/MainTitle";
import {EventsData} from "./eventsData";

const FirstSection = () => {
  const captionStyle = {
    fontSize: "3em",
    fontWeight: "normal",
  };

  const NuestraPropuesta = () => {
    return (
      <>
        <MainTitle
          textContent="Nuestra propuesta"
          description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer vel auctor elit. Praesent non est libero. Donec vel orci nec ex egestas varius. Nam non tellus ut mauris finibus malesuada sed ut nunc."
        />
        <Grid
          container
          rowSpacing={5}
          columnSpacing={3}
          sx={{ marginBottom: "10em" }}
        >
          <Grid item xs={12} md={6} sx={{ textAlign: "center" }}>
            <img className="imgPropuesta" src={imgPropuesta} alt="lol" />
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="body1" gutterBottom sx={{ marginTop: "-5px" }}>
              Lorem ipsum dolor sit amet{" "}
              <span className="highLights">consectetur adipisicing elit</span>.
              Saepe earum eveniet vero nulla, consequuntur illo, sed ratione
              debitis blanditiis eius nisi assumenda? Inventore, illo nemo quasi
              vel minus a? In? Consequatur nemo nam vel! In quibusdam porro amet
              ex iste possimus assumenda quasi a laboriosam? Necessitatibus
              molestiae officiis vel delectus praesentium recusandae optio
              libero odio aliquam, consequuntur cum enim blanditiis.
            </Typography>
          </Grid>
        </Grid>
      </>
    );
  };

  const RecomendadoChef = () => {
    return (
      <>
        <MainTitle
          textContent="Los recomendados del chef"
          description="Lorem ipsum dolor sit amet consectetur adipisicing elit. Nostrum adipisci unde assumenda in odio facilis, distinctio autem accusantium libero quas consequatur commodi.
          debitis blanditiis eius nisi assumenda? Inventore, illo nemo quasi
              vel minus a? In? Consequatur nemo nam vel! In quibusdam porro amet
              ex iste
          "
        />
        <Grid container rowSpacing={5} columnSpacing={3}>
          <Grid item xs={12} md={12}>
            <img loading="lazy" className="imgRecomendados" src={imgRecomendados} alt="lol" />
            <MainButton buttonName="Hacer pedido" />
          </Grid>
        </Grid>
      </>
    );
  };

  const Eventos = () => {
      return (
          <>
          <MainTitle
            textContent="Organizamos tus eventos"
            description="Lorem ipsum dolor sit amet consectetur adipisicing elit. Nostrum adipisci unde assumenda in odio facilis, distinctio autem accusantium libero quas consequatur commodi.
            debitis blanditiis"
          />
          <Grid container rowSpacing={2} columnSpacing={3}>
              {EventsData.map((data, index)=>{
                  return(
                      <React.StrictMode key={index}>
                      <Grid item xs={12} md={3}>
                          <Typography sx={{fontSize:'2em',textAlign:'center'}}>
                              {data.icon}
                          </Typography>
                          <Typography variant="h5" sx={{textAlign:'center'}}>
                              {data.title}
                          </Typography>
                          <Typography variant="body2" sx={{marginTop: "2em", textAlign:'center'}}>
                              {data.description}
                          </Typography>
                      </Grid>
                      </React.StrictMode>

                  )
              })}
          </Grid>
          </>
      )
  }

  return (
    <div id="#1" className="firstSection">
      <Carousel
        data={CarouselData}
        time={4000}
        width="100%"
        height="100vh"
        captionStyle={captionStyle}
        radius="0"
        slideNumber={false}
        captionPosition="center"
        automatic={true}
        dots={true}
        pauseIconColor="white"
        pauseIconSize="5rem"
        slideBackgroundColor="darkgrey"
        slideImageFit="cover"
        thumbnails={false}
        style={{
          textAlign: "center",
          maxWidth: "100%",
          maxHeight: "auto",
          borderRadius: 0,
        }}
      />
      <Container>
        <NuestraPropuesta />
        <RecomendadoChef />
        <Eventos />
      </Container>
    </div>
  );
};

export default FirstSection;
